import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const Profile = () => import('@/components/Profile')
const Education = () => import('@/components/Education')
const Profession = () => import('@/components/Profession')
const SkillPie = () => import('@/components/SkillPie')
const Skills = () => import('@/components/Skills')
const Career = () => import('@/components/Career')

export default new Router({
  routes: [
    {
      path: '/',
      components: {
        Profile: Profile,
        Education: Education,
        Profession: Profession,
        SkillPie: SkillPie,
        Skills: Skills,
        Career: Career
      }
    },
    {
      path: '/Profile',
      name: 'Profile',
      component: Profile
    },
    {
      path: '/Education',
      name: 'Education',
      component: Education
    },
    {
      path: '/Profession',
      name: 'Profession',
      component: Profession
    },
    {
      path: '/SkillPie',
      name: 'SkillPie',
      component: SkillPie
    },
    {
      path: '/Skills',
      name: 'Skills',
      component: Skills
    },
    {
      path: '/Career',
      name: 'Career',
      component: Career
    }
  ]
})
